import { FitAddon } from '@xterm/addon-fit'
import { useEffect, useState, useRef } from 'react'
import { useXTerm } from 'react-xtermjs'
import { Logos } from './ConsoleAdventure/Game/Assets/Assets'
import { ConsoleImplementation_x86 } from './ConsoleAdventure/Game/Base/ConsoleHelp'
import { DefaultColors } from './ConsoleAdventure/Game/Base/ConsoleHelp'
import { Game, GameStates } from './ConsoleAdventure/Game/GameAsync'

const TerminalComponent = () => {
    const { instance, ref } = useXTerm()
    const fitAddon = new FitAddon()
    const logs = Logos.MattediWorks
    const CH = new ConsoleImplementation_x86();
    const setupOnce = useRef(false)
    const game = useRef(null)

    useEffect(() => {
        // Load the fit addon
        if (!setupOnce.current && instance) {
            game.current = new Game();
            instance.options = {
                convertEol: true,
                fontSize: 20,
                backgroundColor: "000000",

            }
        
            setupOnce.current = true
            instance?.loadAddon(fitAddon)
            instance?.resize(120, 40)
            const handleResize = () => fitAddon.fit()
            instance?.onKey(key => {
                let command = key.name;
               // console.log(key.domEvent.key.toLowerCase());
                if (key.key) {
                    if (key.domEvent.key.toLowerCase() == " ")
                        game.current.handleInput("space");
                    else
                        game.current.handleInput(key.domEvent.key.toLowerCase());
                }
                GameStates.getInstance().currentState?.render();
            })
            fitAddon.fit()
            window.onresize = () => {
                fitAddon.fit()
                GameStates.getInstance().currentState?.rerender();
            }
        
            //Load the fit addon
            CH.getWidth = () => {
                return instance?.cols
            }
            CH.write = (text) => {
               
                instance?.write(text)
            }
            CH.print = (text) => {
                if (!text) 
                    text = ''
                // text = text.replace('\n', '\r\n')
                console.log(text)
                CH.write(text + "\n")
            }
            CH.clear_screen = () => {
                instance?.reset()
            }
            Logos.animate(Logos.ConsoleAdventure, 15,
                { color: DefaultColors.YELLOW, index: Logos.ca_cutoff, bgcolor: DefaultColors.GREEN },
                true,
                () => {
                    setTimeout(() => {
                    CH.pressSpace();
                    GameStates.getInstance().currentState = game.current.playerCreation
                    GameStates.getInstance().currentState.rerender()
                    CH.clear_screen();
                    }, 3000)
                }
            )




            // Handle resize event
            window.addEventListener('resize', handleResize)
            return () => {
                window.removeEventListener('resize', handleResize)
            }
        }
    }, [ref, instance])

    return <div ref={ref} style={{ height: '100%', width: '80%' }} />
}

export default TerminalComponent